* {
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    outline: none;
}

.arimac_logo {
    position: fixed;
    width: 65px;
    height: 73px;
    z-index: 20;
    top: calc(100vh - 90px);
    left: 15px;
    background-size: contain;
}

.arimac_logo_white {
    background-image: url("arimac_white.png");
}

.arimac_logo_black {
    background-image: url("arimac_black.png");
}

@media (orientation: portrait) {
    .arimac_logo {
        width: 40px;
        height: 45px;
        top: calc(100vh - 120px);
        left: 20px;
    }
}

/* ====================================================================== */

.arimac_load {
    width: 100vw;
    height: 100vh;
    z-index: 21;
    position: fixed;
    top: 0px;
    left: 0px;
    transition: opacity 0.3s;
}

.arimac_load > canvas {
    position: fixed;
    z-index: 22;
    top: calc(50vh - 80px);
    left: calc(50vw - 80px);
}

.arimac_load > .arimac_lval {
    position: fixed;
    height: 30px;
    line-height: 30px;
    left: 15px;
    top: calc(100vh - 36px);
    font-weight: bold;
    font-size: 13px;
    color: rgba(255, 255, 255, 0.5);
}

.arimac_load_white > .arimac_lval {
    color: #444 !important;
}

@media (orientation: portrait) {
    .arimac_load > canvas {
        top: calc(50vh - 120px);
    }
    .arimac_load > .arimac_lval {
        width: 200px;
        left: calc(50vw - 100px);
        top: calc(100vh - 130px);
        text-align: center;
    }
}